.confirmation-modal {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -webkit-box-shadow: 0 0 0 100vmax rgba(100,100,100, .7);
          box-shadow: 0 0 0 100vmax rgba(100,100,100, .7);
  font-size: 18px;
}

.confirmation-modal__header {
  text-align: center;
  font-size: 24px;
  padding: 25px 25px 15px 25px;
}

.confirmation-modal__content {
  text-align: center;
  padding: 0 25px 25px;
}

.confirmation-modal__footer {
  background-color: rgb(249, 249, 245);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 15px 20px;
  border-top: 1px solid #ccc;
}

.confirmation-modal__footer__button--close {
  font-size: 18px;
  border: none;
  background: none;
  cursor: pointer;
}

.confirmation-modal__footer__button--confirm {
  font-size: 18px;
  color: #fff;
  background-color: rgb(16, 190, 180);
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
}




